.TodoItem{
    padding: 0.5em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.TodoItem>input[type="checkbox"]{
    display: none;
}

.TodoItem>.title{
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 0 0.5em;
}

.TodoItem>.completed~.title{
    text-decoration: line-through;
    color: #999;
}

.TodoItem>button{
    border: none;
    background-color: transparent;
}

.TodoItem i{
    color: #ff5555;
}

.TodoItem i:hover{
    color: #ffcc99;
}


