.App {
  color: #333;
  border: 1px solid #ccc;
  border-radius: 5px;
  background: #fff;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 32em;
  -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
          box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}

.App>h1>button{
  background: #fff;
  border: 1px solid #ff9999;
  padding: 3px 5px;
  border-radius: 3px;
  margin-left: 10px;
  cursor: pointer;
}

.App>h1>button:hover{
  -webkit-box-shadow: 0 0 2px 1px #ffcccc;
          box-shadow: 0 0 2px 1px #ffcccc;
  color: #ff6666;
}

.App>.todoList>li:nth-child(odd){
  background: #ffdddd;
}

.App>.todoList>li:nth-child(even){
  background: transparent;
}


